export default {
	props: {
		info: {
			type: Array
		},
		showlike: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {};
	},
	methods: {
		phone(e) {
			let index = e.currentTarget.dataset.index;
			let tel = this.info[index].shop_phone;
			if (!tel) {
				uni.showToast({
					title: '该商家尚未留下联系方式',
					icon: 'none',
					position: 'bottom'
				});
				return;
			}
			uni.makePhoneCall({
				phoneNumber: tel
			});
		},
		like(e) {
			let index = e.currentTarget.dataset.index;
			this.$emit('tapLike', index);
		},
		handleTap(e) {
			let index = e.currentTarget.dataset.index;
			console.log('点击item');
			this.$emit('tapItem', index);
		}
	}
};
